export default {
	props: {
		// 

		value: {
			type: Array,
			default: () => []
		},







		// idid
		// {id: 1, name: 'uv-ui'}id
		idKey: {
			type: String,
			default: 'id'
		},
		// ms
		addTime: {
			type: Number,
			default: 200
		},
		// 25
		columnCount: {
			type: [Number, String],
			default: 2
		},
		// 20
		columnGap: {
			type: [Number, String],
			default: 20
		},
		// 
		leftGap: {
			type: [Number, String],
			default: 0
		},
		// 
		rightGap: {
			type: [Number, String],
			default: 0
		},
		// nvue
		showScrollbar: {
			type: [Boolean],
			default: false
		},
		// nvue
		columnWidth: {
			type: [Number, String],
			default: 'auto'
		},
		// nvue
		width: {
			type: [Number, String],
			default: ''
		},
		// nvue
		height: {
			type: [Number, String],
			default: ''
		},
		...uni.$uv?.props?.waterfall
	}
}