import * as index from '../function/index.js';
import * as test from '../function/test.js';
import route from '../util/route.js';
import debounce from '../function/debounce.js';
import throttle from '../function/throttle.js';
export default {
	// 
	props: {
		// 
		customStyle: {
			type: [Object, String],
			default: () => ({})
		},
		customClass: {
			type: String,
			default: ''
		},
		// 
		url: {
			type: String,
			default: ''
		},
		// 
		linkType: {
			type: String,
			default: 'navigateTo'
		}
	},
	data() {
		return {}
	},
	onLoad() {
		// getRect$uvin(this)
		this.$uv.getRect = this.$uvGetRect
	},
	created() {
		// createdcreated$uv
		this.$uv.getRect = this.$uvGetRect
	},
	computed: {
		$uv() {
			return {
				...index,
				test,
				route,
				debounce,
				throttle,
				unit: uni?.$uv?.config?.unit
			}
		},
		/**
		 * bem
		 * H5nvueclass:class="[bem()]"
		 * ['a', 'b', 'c']'a b c'
		 * @param {String} name 
		 * @param {Array} fixed 
		 * @param {Array} change truefalse
		 * @returns {Array|string}
		 */
		bem() {
			return function(name, fixed, change) {
				// 
				const prefix = `uv-${name}--`
				const classes = {}
				if (fixed) {
					fixed.map((item) => {
						// 
						classes[prefix + this[item]] = true
					})
				}
				if (change) {
					change.map((item) => {
						// this[item]truefalse
						this[item] ? (classes[prefix + item] = this[item]) : (delete classes[prefix + item])
					})
				}
				return Object.keys(classes)
					// ","



			}
		}
	},
	methods: {
		// 
		openPage(urlKey = 'url') {
			const url = this[urlKey]
			if (url) {
				// uni.navigateTo
				uni[this.linkType]({
					url
				})
			}
		},
		// 
		// bug(2020-07-21)
		// view
		$uvGetRect(selector, all) {
			return new Promise((resolve) => {
				uni.createSelectorQuery()
					.in(this)[all ? 'selectAll' : 'select'](selector)
					.boundingClientRect((rect) => {
						if (all && Array.isArray(rect) && rect.length) {
							resolve(rect)
						}
						if (!all && rect) {
							resolve(rect)
						}
					})
					.exec()
			})
		},
		getParentData(parentName = '') {
			// createdparent
			if (!this.parent) this.parent = {}
			// (uv-radiouv-radio-groupthis)
			// this(uv-radiothis)parentData
			// this.parent.xxx
			// uv-radio-groupdata
			this.parent = this.$uv.$parent.call(this, parentName)
			if (this.parent.children) {
				// childrenchildren
				this.parent.children.indexOf(this) === -1 && this.parent.children.push(this)
			}
			if (this.parent && this.parentData) {
				// parentDataparentparentData
				Object.keys(this.parentData).map((key) => {
					this.parentData[key] = this.parent[key]
				})
			}
		},
		// 
		preventEvent(e) {
			e && typeof(e.stopPropagation) === 'function' && e.stopPropagation()
		},
		// 
		noop(e) {
			this.preventEvent(e)
		}
	},
	onReachBottom() {
		uni.$emit('uvOnReachBottom')
	},
	beforeDestroy() {
		// parentchldrencheckboxcheckbox-group
		// children
		if (this.parent && test.array(this.parent.children)) {
			// children
			const childrenList = this.parent.children
			childrenList.map((child, index) => {
				// 
				if (child === this) {
					childrenList.splice(index, 1)
				}
			})
		}
	},
	// vue3
	unmounted() {
		if (this.parent && test.array(this.parent.children)) {
			// children
			const childrenList = this.parent.children
			childrenList.map((child, index) => {
				// 
				if (child === this) {
					childrenList.splice(index, 1)
				}
			})
		}
	}
}